@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
.containerTitle{
    text-align: center;
    margin: 70px 0px 30px 0px;
    font-size: 60px;
}

.new{
    display: flex;
    /* flex-direction: row; */
    grid-gap: 37px;
    gap: 37px;
    align-items: center;
    justify-content: center;
}

.new img{
    width: 112px;
    height: 112px;
    margin-top: 40px;
}

@media screen and (max-width: 600px) {
    .new{
        scale: 0.7;
    }
  }
.slider {
  width: 100%;
  height: 90vh;
  position: relative;
  overflow: hidden;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: all 0.5s ease;
}

@media screen and (min-width: 600px) {
  .slide img {
    width: 100%;
    height: 100%;
  }
}

.slide img {
  height: 100%;
}

.current {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.content {
  position: absolute;
  /* top: 23rem; */
  margin-top: -20%;
  left: 10px;
  scale: 77%;
  opacity: 0;
  width: 50%;
  color: #fff;
  padding: 10px;
  background: rgba(0, 0, 0, 0.3);
  -webkit-animation: slide-up 1s ease 0.5s;
          animation: slide-up 1s ease 0.5s;
  border-radius: 27px;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  visibility: hidden;
}

@-webkit-keyframes slide-up {
  0% {
    visibility: visible;
    top: 23rem;
  }
  100% {
    visibility: visible;
    top: 17rem;
  }
}

@keyframes slide-up {
  0% {
    visibility: visible;
    top: 23rem;
  }
  100% {
    visibility: visible;
    top: 17rem;
  }
}

@media screen and (max-width: 600px) {
  .content {
    width: 80%;
  }
}

.content > * {
  color: #fff;
  /* margin-bottom: 1rem; */
}

.current .content {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: all 0.5s ease;
}

.arrow {
  border: 2px solid white;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  position: absolute;
  z-index: 999;
  font-size: 2.7rem !important;
  transition: scale 0.3s ease 0s !important;
}
.arrow:hover {
  background-color: #fff;
  color: #777;
  scale: 1.3 !important;
}

.next {
  top: 35%;
  right: 1.5rem;
}
.prev {
  top: 35%;
  left: 1.5rem;
}

hr {
  height: 2px;
  background: white;
  width: 50%;
}

.content h1 {
  font-size: 2.7rem;
}
.content p {
  font-size: 1.5rem;
}

h1,
h2,
h3,
h4 {
  letter-spacing: 0.1rem;
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
.section {
  width: 90vw;
  margin: 5rem auto;
  max-width: 1170px;
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}

.icon {
  font-size: 3rem;
  color: hsl(21, 62%, 45%);
}

.section-center {
  margin: 0 auto;
  margin-top: 4rem;
  width: 80vw;
  height: 450px;
  max-width: 800px;
  text-align: center;
  position: relative;
  display: flex;
  overflow: hidden;
}

article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
}

article.activeSlide {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
article.lastSlide {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
article.nextSlide {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.person-img {
  border-radius: 50%;
  margin-bottom: 1rem;
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 4px solid hsl(210, 31%, 80%);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

article h4 {
  text-transform: uppercase;
  color: hsl(21, 62%, 45%);
  margin-bottom: 0.25rem;
}
.title {
  text-transform: capitalize;
  margin-bottom: 0.75rem;
  color: hsl(209, 34%, 30%);
}

.title {
  text-align: center;
  margin-bottom: 2rem;
}
.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}

.text {
  max-width: 35em;
  margin: 0 auto;
  margin-top: 2rem;
  line-height: 2;
  color: hsl(210, 22%, 49%);
}

.prevBtn,
.nextBtn {
  position: absolute;
  top: 200px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: hsl(210, 22%, 49%);
  color: #fff;
  width: 1.25rem;
  height: 1.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.3s linear;
}
.prevBtn:hover,
.nextBtn:hover {
  background: hsl(21, 62%, 45%);
}
.prevBtn {
  left: 0;
}
.nextBtn {
  right: 0;
}
@media (min-width: 800px) {
  .text {
    max-width: 45em;
  }
  .prevBtn,
  .nextBtn {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}

