* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}

:root {
  --mainColor: #29335c;
  --mainColorLight: #5767aa;
  --secondaryColor: #db2b39;
  --textColor: #eee;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: #29335c;
  background-color: var(--mainColor);
  color: #eee;
  color: var(--textColor);
}

nav a {
  margin: 0 1rem;
  color: #eee;
  color: var(--textColor);
  text-decoration: none;
}

nav a:hover {
  color: #db2b39;
  color: var(--secondaryColor);
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: #eee;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}

header div,
nav {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    background-color: #29335c;
    background-color: var(--mainColor);
    transition: 1s;
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
  }

  header .responsive_nav {
    -webkit-transform: none;
            transform: none;
    z-index: 1000;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }

  nav a {
    font-size: 1.5rem;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.productItemWrapper {
  display: grid;
  grid-template-columns: repeat(2, 400px);
  grid-template-rows: repeat(2, 300px);
  align-items: center;
  grid-row-gap: 7vh;
  row-gap: 7vh;
  justify-content: center;
  justify-items: center;
  min-height: 102vh;
  background: #efefef;
  background: linear-gradient(45deg, #fbda61, #ff5acd);
}

@media screen and (max-width: 870px) {
  .productItemWrapper {
    grid-template-columns: repeat(1, 400px);
  }
}

.productItem {
  position: relative;
  margin-top: 30%;
  width: 350px;
  height: 190px;
  background: #fff;
  border-radius: 20px;
  transition: 0.5s;
  box-shadow: 0 35px 80px rgba(0, 0, 0, 0.15);
}
@media screen and (max-width: 400px) {
  .productItem {
    width: 70%;
  }
}
@media screen and (max-width: 300px) {
  .productItem {
    width: 50%;
  }
  .imgBx img {
    scale: 0.9;
  }
}
.productItem:hover {
  height: 450px;
}
.productItem .imgBx {
  position: absolute;
  left: 50%;
  top: -50px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 150px;
  height: 150px;
  border-radius: 20px;
  /* box-shadow: 0 15px 50px rgba(0, 0, 0, 0.35); */
  /* overflow: hidden; */
  transition: 0.5s;
  z-index: 10;
}
.productItem:hover .imgBx {
  width: 250px;
  height: 250px;
}
.productItem .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* object-fit: cover; */
}
.productItem .productContent {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
}
.productItem .Itemdetails {
  padding: 40px 40px;
  text-align: center;
  width: 100%;
  transition: 0.5s;
  -webkit-transform: translateY(150px);
          transform: translateY(150px);
}
.productItem:hover .Itemdetails {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}
.productItem .Itemdetails h2 {
  font-size: 1.25em;
  font-weight: 600;
  color: #555;
  line-height: 1.2em;
}
.productItem .Itemdetails h2 span {
  font-size: 0.75em;
  font-weight: 500;
  opacity: 0.5;
}
.productItem .Itemdetails .Itemdata {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}
.productItem .Itemdetails .Itemdata h3 {
  font-size: 1em;
  color: #555;
  line-height: 1.2em;
  font-weight: 600;
  text-align: center;
}
.productItem .Itemdetails .Itemdata h3 span {
  font-size: 0.85em;
  font-weight: 400;
  text-align: center;
  opacity: 0.5;
}
.productItem .Itemdetails .itemactionBtn button {
  padding: 10px 30px;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 1em;
  font-weight: 500;
  background: #ff5f95;
  color: #fff;
  cursor: pointer;
}

.imgBx img {
  border-radius: 50%;
}

